<template>
  <div>
    <ul class="tag" v-if="showTag">
      <router-link to="/views/researchProjects/country">
        <li :class="{ active: state == 1 }" @click="selectTag(1)">国家级项目</li>
      </router-link>

      <router-link to="/views/researchProjects/province">
        <li :class="{ active: state == 2 }" @click="selectTag(2)">省部级项目</li>
      </router-link>

      <router-link to="/views/researchProjects/other">
        <li :class="{ active: state == 3 }" @click="selectTag(3)">其他纵向项目</li>
      </router-link>

      <router-link to="/views/researchProjects/landscape">
        <li :class="{ active: state == 4 }" @click="selectTag(4)">横向项目</li>
      </router-link>
    </ul>
    <div v-if="showTag" class="tagNull"></div>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'researchProjects',
  data () {
    return {
      state: 1,
      tagsUrl: [
        '/views/researchProjects/country',
        '/views/researchProjects/province',
        '/views/researchProjects/other',
        '/views/researchProjects/landscape'
      ],
      showTag: false
    }
  },
  methods: {
    selectTag (state) {
      if (state == this.state) {
        return
      }
      this.state = state
    }
  },
  created () {
    const arrIndex = $.inArray(this.$route.path, this.tagsUrl)
    if (arrIndex > -1) {
      this.showTag = true
      this.state = arrIndex + 1
    }
  }
}
</script>



